/* -------------------------

Title: aside navigation
Descr: asideN small and large, hovers and toggles for subNav

--------------------------- */

.asideNavWrapper {
  background: var(--as-white);
  position: inherit;
  @include large() {
    background: none;
    position: sticky;
    top: 0;
  }
  font-size: var(--base-16);
  animation: none;
  // top: 0;
  // position: sticky;
  h4 {
    font-size: var(--base-16);
    margin-right: 1em;
    @include medium() {
      font-size: var(--base-20);
    }
  }
  &.stuck {
    animation-name: asideNavFader;
    animation-duration: 4s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    display: flex;
    width: 100%;
    @include large() {
      display: block;
      animation: none;
      // position: sticky;
      // top: 0;
    }
  }
}

@keyframes asideNavFader {
  0% {
    opacity: 0;
  }
  20% {
    position: fixed;
    top: 0;
  }
  30% {
    opacity: 0;
    position: fixed;
    top: 0;
  }
  100% {
    opacity: 1;
    position: fixed;
    top: 0;
  }
}

.asideNav {
  font-size: var(--base-16);
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  padding-left: 0;
  flex-direction: column;
  @include medium() {
    flex-direction: row;
    flex-shrink: 0;
  }
  @include large() {
    font-size: var(--base-20);
    flex-direction: column;
    list-style-type: circle;
    padding-left: 1em;
  }
  li {
    margin-right: 1em;
    @include large() {
      margin-right: 0;
    }
  }
}
