:root {
  --white: #f0f0f0;
  --black: #2c2c2c;
  --as-white: #fdfdfd;
  --as-black: #2c2c2c;
  --dark-text: #35363f;
  --gray-dark: #707070;
  --gray-med: #959595;
  --gray-lt: #c2c0c0;
  --gray-lightest: #fbfbfb;
  --dusk: #435363;
  --fog: #607774;
  --storm: #4f869f;
  --teal: #85c4ca;
  --gold: #ffd91f;
  --as-green: #4d9b5e;
  --as-red: #b31b1b;
  --gray: #c2c0c0;
  --gray-light: #d9d9d9;
  --near-white: #fbfbfb;
  --xs: 320px;
  --small: 480px;
  --medium: 782px;
  --large: 1224px;
  --xl: 1432px;
  --xxl: 1640px;
  --font-sans: "canada-type-gibson", sans-serif;
  --font-serif: "freight-micro-pro", verdana;
  --font-curly: "Alisha";
  --base-font-size: 20px;
  --medium-font-size: 20px;
  --small-font-size: 16px;
  --sidebar-font-size: 20px;
  --main-font-size: 28px;
  --font-size--huge: 60px;
  --base-16: 0.666rem;
  --base-18: 0.75rem;
  --base-20: 0.8333rem;
  --base-24: 1rem;
  --base-28: 1.166rem;
  --base-36: 1.5rem;
  --base-48: 2rem;
  --base-64: 2.66rem;
  --light: 300;
  --regular: 400;
  --semi: 600;
  --heavy: 800;
  --line-height: 1.4;
  --line-width: 60ch;
  --border-width: 1px;
  --border-color: var(--gray-light);
  --border-radius: 4px;
  --space: 1rem;
  --bottom-margin: 2rem;
  --mainGutter: var(--space);
  --auto-grid-min-size: calc(10 * var(--space));
  --grid-gap: var(--mainGutter);
  --base-grid-column: 84px;
  --component-bottom-margin: calc(1 * var(--space)); }
  @media (min-width: 1224px) {
    :root {
      --base-font-size: 24px; } }
  @media (min-width: 782px) {
    :root {
      --space: 1.5rem; } }
  @media (min-width: 782px) {
    :root {
      --bottom-margin: 4rem; } }
  @media (min-width: 480px) {
    :root {
      --mainGutter: var(--space); } }
  @media (min-width: 782px) {
    :root {
      --mainGutter: calc(2 * var(--space)); } }

/* -------------------------

Title: _base.scss
Descr: sets HTML and Body styles

--------------------------- */
html {
  font-size: var(--base-font-size); }

body {
  font-family: var(--font-sans);
  font-size: var(--base-font-size);
  color: var(--dark-text);
  font-weight: var(--light);
  line-height: var(--line-height);
  overflow-x: hidden; }

p {
  max-width: 100%;
  line-height: calc(1ex / 0.32); }

/* -------------------------

Title: headings.
Descr: 
    Sets basic styles for h1-h4

--------------------------- */
h1,
h2,
h3,
h4 {
  font-family: var(--font-sans);
  font-weight: var(--semi);
  margin: 0 0 0.5em 0; }

h1 {
  font-size: calc(2.5 * var(--base-font-size));
  line-height: calc(1ex / 0.5); }
  h1 .title {
    margin-top: 0; }

h2 {
  font-size: calc(2 * var(--base-font-size));
  line-height: 1em; }

h3 {
  font-size: calc(1.75 * var(--base-font-size)); }

h4 {
  font-size: calc(1.5 * var(--base-font-size)); }

.sidebar h2 {
  font-size: calc(1.5 * var(--base-font-size)); }

/* -------------------------

Title: link
Descr: 
    basic link

--------------------------- */
a,
.link {
  color: var(--storm);
  text-decoration-color: var(--gray-lt); }

/* -------------------------

Title: lists
Descr: Sets grid default <ul> <ol>
Variations: 


--------------------------- */
ul,
ol {
  padding-left: 1rem;
  margin-bottom: 1em; }
  ul li,
  ol li {
    margin-bottom: 0.25em;
    max-width: var(--line-width); }

/* -------------------------

Title: <main> layouts.
Descr: 
    main content is the <main> tag. It is where the main components are. It is a child of the .container.

--------------------------- */
.main {
  max-width: 1432px;
  margin: 0 auto;
  display: grid;
  grid-gap: 2rem;
  grid-row-gap: 0;
  padding: 0 1rem; }
  @media (min-width: 782px) {
    .main {
      grid-template-columns: repeat(12, calc(8.59% - 2rem)); } }

.basic > div,
.landingPage > div {
  grid-column: 1 / -1; }

.basicSidebar {
  grid-template-rows: auto auto 1fr;
  grid-template-areas: "nav" "title" "content" "sidebar"; }
  .basicSidebar .siteNav {
    grid-area: nav; }
  .basicSidebar .title {
    grid-area: title; }
  .basicSidebar .sidebar {
    grid-area: sidebar; }
  .basicSidebar .content {
    grid-area: content; }
  .basicSidebar aside {
    display: none;
    grid-area: aside; }
    @media (min-width: 782px) {
      .basicSidebar aside {
        display: block; } }
    .basicSidebar aside .page_component_entity {
      display: none; }
      @media (min-width: 1224px) {
        .basicSidebar aside .page_component_entity {
          display: block; } }
  @media (min-width: 782px) {
    .basicSidebar {
      grid-template-areas: "nav nav nav nav title title title title title title title title" "nav nav nav nav content content content content content content content content" "sidebar sidebar sidebar sidebar content content content content content content content content"; } }
  @media (min-width: 782px) {
    .basicSidebar--has-aside {
      grid-template-areas: ". . . title title title title title title title . ." "nav nav nav aside aside aside aside aside aside aside aside aside" "nav nav nav content content content content content content content content content" "sidebar sidebar sidebar content content content content content content content content content"; } }
  @media (min-width: 1224px) {
    .basicSidebar--has-aside {
      grid-template-areas: "nav nav nav title title title title title title title . ." "nav nav nav content content content content content content content aside aside" "sidebar sidebar sidebar content content content content content content content aside aside"; } }

/* -------------------------

Title: container layouts.
Descr: 
    this is the container that spans from the <header> to <footer>. It establishes columns for sidebars etc. .mainContent(--modifier) is a child of .container.

    - --has-aside and has--sidebar are the base models. Basic page, and TOC both use them

    -- article and --hasPano are for articles, more complicated grids

--------------------------- */
.container {
  grid-column: 2;
  display: grid;
  align-items: flex-start;
  align-content: flex-start; }
  @media (min-width: 1224px) {
    .container--has-aside {
      grid-gap: 2rem;
      grid-template-columns: 1fr minmax(200px, 300px);
      grid-template-areas: "title title" "components aside"; }
      .container--has-aside .title {
        grid-area: title; }
      .container--has-aside .components {
        grid-area: components; }
      .container--has-aside aside {
        grid-area: aside; } }
  .container--has-sidebar .toc-menu,
  .container--has-sidebar .sidebar-nav {
    grid-area: menu; }
  .container--has-sidebar .sidebar {
    grid-area: sidebar; }
  .container--has-sidebar main {
    grid-area: main; }
  @media (min-width: 1640px) {
    .container--has-sidebar {
      grid-gap: calc(3 * var(--space));
      grid-row-gap: var(--space); } }

.page-title {
  grid-area: page-title;
  display: block; }

.main-content {
  grid-area: content; }

/* -------------------------

Title: Swiss Army 
Descr: Where all little helper classes that don't go on one specific component css will live. They all must be applicable to multiple elements.

--------------------------- */
.hidden {
  display: none; }

.sr-only {
  display: block;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(1px);
  white-space: nowrap;
  position: absolute; }

/* -------------------------

Title: text
Descr: Styles for things that are not anywhere else. Essentially css atoms.

--------------------------- */
strong {
  font-weight: var(--semi); }

/* -------------------------

Title: blockquote
Descr: 
   - style block quote.

--------------------------- */
.pageComponent--blockQuote blockquote {
  background: var(--gray-lightest);
  padding: 3rem;
  font-style: italic;
  font-size: 1.5rem;
  margin: 3rem; }
  .pageComponent--blockQuote blockquote .attribution {
    font-weight: var(--semi); }

.pageComponent--ftb blockquote {
  background: var(--gray-lightest);
  padding: 3rem;
  font-style: italic;
  font-size: 1.5rem;
  margin: 3rem; }
  .pageComponent--ftb blockquote .attribution {
    font-weight: var(--semi); }

/* -------------------------

Title: Buttons
Descr: 
   - iconButton
    -- a button that has an svg icon in the middle

--------------------------- */
.iconButton svg {
  width: 1rem; }

/* 
  Base styles for all figures
  - basic figure
  - figure based on different view modes
  - extra class based on image style to set max widths
*/
figure {
  margin: 1rem 0;
  background: var(--gray-lightest);
  width: 100%; }
  figure.figure--pano {
    margin: 0; }
  figure.figure--thumbnail {
    background: none; }

.pageComponent--ftb .figure--6_4_newsletter {
  max-width: 600px; }

.pageComponent--ftb .figure--5_4 {
  max-width: 520px; }

.pageComponent--ftb .figure--4_5 {
  max-width: 300px; }

figcaption {
  font-weight: var(--light);
  font-size: var(--medium-font-size);
  padding: 1rem; }

.videoContainer {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  height: 0; }

.videoContainer iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

/* -------------------------

Title: image
Descr: 
    Most basic universal image styles

--------------------------- */
img,
picture {
  display: block;
  height: auto;
  max-width: 100%; }

/* -------------------------

Title: odds-n-ends
Descr: I know right...
       
  - alignmets - margins set to mainGutter

--------------------------- */
.align-right {
  margin-left: var(--mainGutter); }

.align-left {
  margin-right: var(--mainGutter); }

/* -------------------------

Title: campfire
Descr: 
    sets up main campfire, three columns, and responsive breakdown

--------------------------- */
.campfire {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  margin-bottom: var(--bottom-margin); }
  @media (min-width: 782px) {
    .campfire {
      flex-direction: row; }
      .campfire__feature {
        flex: 0 0 47%;
        order: 2; }
      .campfire__articles {
        flex: 0 0 23%;
        order: 1; }
        .campfire__articles:last-of-type {
          order: 3; } }

.campfire__support {
  box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.08);
  border-radius: var(--border-radius);
  padding: var(--space); }
  @media (min-width: 1432px) {
    .campfire__support p {
      font-size: 1.25rem; } }
  .campfire__support h2 {
    font-weight: var(--semi);
    margin-top: 0.5em; }

/* -------------------------

Title: _card--article.scss
Descr: Sets all specific styles for an article card
  sets css for card--feature as they are articles

--------------------------- */
.card--article {
  grid-row: span 2; }
  .card--article .card__copy {
    padding: 1rem;
    display: grid;
    grid-gap: 0px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: "label label label" "title title title" "date byline byline"; }
  .card--article .card__title {
    grid-area: title;
    margin: 0.75em 0 2em 0;
    font-weight: var(--regular);
    font-size: var(--base-20);
    line-height: 1.25; }
  .card--article .card__label {
    grid-area: label;
    color: var(--as-black);
    font-size: var(--base-18);
    font-weight: var(--semi); }
  .card--article .card__date {
    grid-area: date;
    text-align: left;
    font-size: var(--base-16);
    margin-top: 2px; }
  .card--article .card__byline {
    grid-area: byline;
    font-size: var(--base-16);
    text-align: right; }
  .card--article.card--small {
    border: none;
    margin-bottom: 2rem; }
    .card--article.card--small .card__copy {
      padding: 0.5rem 0.5rem 1rem 0; }
    .card--article.card--small .card__title {
      grid-area: title;
      margin: 0;
      font-family: var(--font-sans);
      font-size: var(--medium-font-size);
      color: var(--dark-text);
      font-weight: var(--semi);
      line-height: 1.4em; }
    .card--article.card--small .label,
    .card--article.card--small .card__byline,
    .card--article.card--small .card__date {
      display: none; }
  .card--article figcaption {
    display: none; }

.card--feature .card__copy {
  grid-template-columns: 1fr;
  grid-template-areas: "label" "title" "byline"; }

.card--feature a span {
  display: block; }

.card--feature .figure {
  grid-area: image; }

.card--feature .card__title {
  padding: 0 1rem;
  font-size: 2rem;
  text-align: center;
  font-weight: var(--semi); }

.card--feature .card__label {
  color: var(--as-black);
  font-size: var(--base-18);
  font-weight: var(--semi);
  text-align: center; }

.card--feature .card__date {
  font-size: var(--base-16); }

.card--feature .card__byline {
  font-size: var(--base-18);
  text-align: center; }

/* -------------------------

Title: _card--major-minor.scss
Descr: Sets all specific styles for an m/m/g card and teasers 

--------------------------- */
.card--major-minor .card__copy {
  padding: 1rem;
  display: grid;
  grid-gap: 0px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: ". . level" "label label label"; }

.card--major-minor .card__title {
  grid-area: label; }

.card--major-minor .card__level {
  grid-area: level; }

.ul--jellyBeans {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0; }
  .ul--jellyBeans .link {
    text-indent: -5000px;
    position: relative;
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem; }
    .ul--jellyBeans .link:before {
      width: 1.5rem;
      height: 1.5rem;
      border-radius: 50%;
      position: absolute;
      content: "M";
      color: white;
      top: 0;
      right: 0;
      text-indent: 0;
      text-align: center; }
    .ul--jellyBeans .link--major:before {
      background: red;
      content: "M"; }
    .ul--jellyBeans .link--minor:before {
      background: var(--#008080);
      content: "m"; }
    .ul--jellyBeans .link--graduate:before {
      background: var(--#ffd700);
      content: "g"; }

/* -------------------------

Title: Cards
Descr: Sets grid default card styles
Variations: 
  card--small
  card--article - on _card--article.scss
  card--feature - on card--article.scss
  card--stat

  card--no-image

--------------------------- */
.card {
  box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.08);
  border-radius: var(--border-radius);
  margin-bottom: var(--space); }
  .card figure {
    margin: 0; }

.card__title {
  font-size: 1rem;
  line-height: 1.3;
  font-weight: var(--semi); }
  .card__title-link {
    color: var(--storm);
    text-decoration-color: var(--gray-lightest); }

.card__label {
  margin-bottom: 0;
  color: var(--gray-dark); }

.card__body {
  margin: 0.7em 0 1em; }

.card--no-image .card__copy {
  grid-column: 1 / -1; }

[data-embed-button="article_entity_embed"] .card--small {
  max-width: 400px; }

.card--course {
  padding: 1rem; }

/* -------------------------

Title: Event List
Descr: Sets grid default ( full width ) and stacked (in columns) event lists
        Sets all default individual event listings

--------------------------- */
.eventList {
  display: grid;
  grid-gap: var(--grid-gap);
  margin: 0 0 var(--component-bottom-margin) 0; }
  @media (min-width: 782px) {
    .eventList {
      grid-template-columns: repeat(auto-fit, minmax(320px, 1fr)); } }

.columns .eventList {
  grid-template-columns: auto; }

.event {
  display: grid;
  grid-gap: var(--space);
  grid-row-gap: 0;
  grid-template-columns: 84px 1fr;
  grid-template-areas: "date content"; }
  .event__dateTime {
    grid-area: date;
    text-align: center; }
  .event__month {
    font-weight: var(--heavy); }
  .event__date {
    font-weight: var(--semi);
    border-bottom: 1px solid var(--gray-lt);
    margin-bottom: 0.25rem;
    padding-bottom: 0.25rem; }
  .event__content {
    grid-area: content; }
  .event__title {
    grid-area: title; }
  .event__location {
    grid-area: location; }
  .event span {
    display: block; }

/* -------------------------

Title: link block
Descr: sets background, color options, link styles for link blocks

--------------------------- */
.linkBlock {
  background: var(--gray-med);
  color: var(--as-white);
  text-align: center;
  padding: 2em;
  border-radius: var(--border-radius); }
  .linkBlock a {
    display: inline-block;
    background: var(--as-white);
    padding: 0.5em 1em;
    border-radius: var(--border-radius);
    font-weight: var(--semi);
    margin-right: 1em; }
    .linkBlock a:last-of-type {
      margin-right: 0; }

/* -------------------------

Title: teasers
Descr: Sets grid default teaser styles
Variations: 
  teaser
** still have to figure out why teasers with summaeries  are differnet sizes
--------------------------- */
.teaser {
  display: flex;
  align-items: flex-start;
  margin-bottom: var(--bottom-margin); }
  .teaser figure {
    margin-right: 1rem; }

.teaser--article figure {
  max-width: 200px; }
  .teaser--article figure img {
    width: 8rem; }

/* -------------------------

Title: _forms.scss
Descr: 
    Main inpage forms

    .form--simpleSearch

    .form--overlaySearch
    .form--blockSearch

--------------------------- */
.form--simpleSearch {
  display: inline-flex; }
  .form--simpleSearch .searchField {
