.form--overlaySearch {
  justify-content: flex-end;
  align-items: center;
  opacity: 0;
  visibility: hidden; }
  .form--overlaySearch.shown {
    opacity: 1;
    visibility: visible; }

.form-item {
  color: var(--#808080);
  margin-bottom: 1em;
  max-width: 32em;
  font-size: 0.75rem; }

label,
.form-item__label {
  display: block;
  font-weight: 600; }
  @media (min-width: 320px) {
    label,
    .form-item__label {
      display: block;
      margin-right: 2%;
      padding: 0.6em 0; } }

.form-item__textfield,
.form-select,
.form-autocomplete {
