  flex-direction: column; }

/* -------------------------

Title: aside navigation
Descr: asideN small and large, hovers and toggles for subNav

--------------------------- */
.asideNavWrapper {
  background: var(--as-white);
  position: inherit;
  font-size: var(--base-16);
  animation: none; }
  @media (min-width: 1224px) {
    .asideNavWrapper {
      background: none;
      position: sticky;
      top: 0; } }
  .asideNavWrapper h4 {
    font-size: var(--base-16);
    margin-right: 1em; }
    @media (min-width: 782px) {
      .asideNavWrapper h4 {
        font-size: var(--base-20); } }
  .asideNavWrapper.stuck {
    animation-name: asideNavFader;
    animation-duration: 4s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    display: flex;
    width: 100%; }
    @media (min-width: 1224px) {
      .asideNavWrapper.stuck {
        display: block;
        animation: none; } }

@keyframes asideNavFader {
  0% {
    opacity: 0; }
  20% {
    position: fixed;
    top: 0; }
  30% {
    opacity: 0;
    position: fixed;
    top: 0; }
  100% {
    opacity: 1;
    position: fixed;
    top: 0; } }

.asideNav {
  font-size: var(--base-16);
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  padding-left: 0;
  flex-direction: column; }
  @media (min-width: 782px) {
    .asideNav {
      flex-direction: row;
      flex-shrink: 0; } }
  @media (min-width: 1224px) {
    .asideNav {
      font-size: var(--base-20);
      flex-direction: column;
      list-style-type: circle;
      padding-left: 1em; } }
  .asideNav li {
    margin-right: 1em; }
    @media (min-width: 1224px) {
      .asideNav li {
        margin-right: 0; } }

/* -------------------------

Title: main navigation
